nav{
    background: rgba(223, 214, 214, 0.3);
    width: max-content;

    padding: 0.7rem 1.7rem;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius:50% ;
    display: flex;
    color: #f64c72 ;
    font-size: 1.1rem;

}


nav a:hover{
    background: rgba(0, 0, 0, 0.3);

}

nav a.active{
    background:var(--color-bg);
    color: var(--color-white);
}